                <el-input type="textarea" v-model="form.attentions" placeholder="50"></el-input>
            </el-form-item>
            <el-form-item label="" class="is-required">
                <div class="plan" v-for="(plan, index) in form.plan">
                    <div class="line">
                        <el-form-item label="">
                            <el-input v-model="plan.name" placeholder=""></el-input>
