                            <div class="line-list" v-for="(info, i) in plan.planInfo" :key="i">
                                <el-input v-model="info.text" placeholder=""></el-input>
                                <div class="btn">
                                    <el-button type="primary" size="small" plain @click="addPlanInfo(index, i+1)"></el-button><el-button type="danger" size="small" plain @click="deletePlanInfo(index, i)" v-if="i > 0"></el-button>
                                </div>
                            </div>
                        </el-form-item>
                    </div>
                    <div class="line">
                        <div class="addBtn">
                            <el-button type="success" plain @click="addPlan(index)"></el-button>
                            <el-button type="danger" plain @click="deletePlan(index)" v-if="index > 0"></el-button>
                        </div>
                    </div>
                </div>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit('form', 'draft')"></el-button>
                <el-button type="success" @click="onSubmit('form', '')"></el-button>
            </el-form-item>
        </el-form>
