</template>

<script>
    import {createMarks} from "../api/getdata"

    export default {
        name: "Entry",
        data() {
            return {
              form: {
                type: '',
                status: '', //  active|off
                name: '',  // 
                logo: '',  // logo
                platform: '', // 
                score: '', // 
                description: '', // 
                online: '', // 
                finance: '', // 
                level: '', // 
                projectUrl: '', // 
                tags: '', // 
                attentions: '', // 
                plan: [{
                  name: '', // 
                  timeLimit: '', // ()
                  targetProject: '', // 
                  money: '', // ()
                  giveBack: '', // ()
                  yearlyProfit: '', // (%)
                  sumProfit: '', // ()
                  serial: '', // 
                  planInfo: [{
                      index: 0, // , 
                      text: '', // 
                  }], // 
                }]
              },
              rules: {
                type: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                logo: [
                  { required: true, message: 'logo', trigger: 'blur' }
                ],
                name: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                platform: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                score: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                description: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                online: [
                  { required: true,  message: '', trigger: 'blur' }
                ],
                finance: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                projectUrl: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                tags: [
                  { required: true, message: '', trigger: 'blur' }
                ],
                attentions: [
                  { required: true, message: '', trigger: 'blur' }
                ]
              }
            }
        },
        methods: {
            onSubmit(ruleForm, type) {
              this.form.status = 'draft'
              let data = this.form;
              let createMark = createMarks(data)
              this.$refs[ruleForm].validate((valid) => {
                if (valid) {
                  createMark.then(res => {
                    this.$message({
                      message: type == 'draft' ? '' : '',
                      type: 'success'
                    });
                    this.$refs[ruleForm].resetFields()
                  })
                } else {
                  return false;
                }
              });
            },
            addPlan(index) {
              let plan = {
                    name: '', // 
                    timeLimit: '', // ()
                    targetProject: '', // 
                    money: 0, // ()
                    giveBack: '', // ()
                    yearlyProfit: '', // (%)
                    sumProfit: '', // ()
                    serial: 0, // 
                    planInfo: [{
                      index: 0, // , 
                      text: '', // 
                    }]
              }
              console.log(this.form.plan[index])
                this.form.plan.push(plan)
            },
            deletePlan(index) {
              if (index > 0) {
                this.form.plan.splice(index, 1)
              } else {
                this.$message.error('');
              }
            },
            addPlanInfo(index, i) {
              let planInfo = {index: i, text: ''}
              this.form.plan[index].planInfo.push(planInfo)
              console.log(this.form.plan[index].planInfo)
            },
            deletePlanInfo(index, i) {
              if (i > 0) {
                this.form.plan[index].planInfo.splice(i, 1)
              } else {
                this.$message.error('');
              }
            },
            uploadImg(res, file) {
                if (res.output) {
                    this.form.logo = res.output.path
                }else{
                    this.$message.error('');
                }
            },
            beforeImgUpload(file) {
                const isRightType = (file.type === 'image/jpeg') || (file.type === 'image/png');
                const isLt2M = file.size / 1024 / 1024 < 2;

                if (!isRightType) {
                    this.$message.error(' JPG/PNG !');
                }
                if (!isLt2M) {
                    this.$message.error(' 2MB!');
                }
                return isRightType && isLt2M;
            },
        }
    }
</script>

