<template>
    <div class="entry">
        <el-form ref="form" :model="form" :rules="rules" label-width="80px">
            <el-form-item label="" prop="type">
                <el-radio v-model="form.type" label=""></el-radio>
                <el-radio v-model="form.type" label=""></el-radio>
            </el-form-item>
            <el-form-item label="logo" prop="logo">
                <el-upload
                        class="avatar-uploader"
                        action="https://easy-mock.com/mock/5b1143e016e1e93c6ab6fe00/admin/admin/tool/file"
                        :show-file-list="false"
                        :on-success="uploadImg"
                        :before-upload="beforeImgUpload">
                    <img v-if="form.logo" :src="form.logo" class="avatar">
                    <el-button v-else type="primary" ></el-button>
                </el-upload>
            </el-form-item>
            <div class="line">
                <el-form-item label="" prop="name">
                    <el-input v-model="form.name" placeholder="+3"></el-input>
                </el-form-item>
                <el-form-item label="" prop="platform">
                    <el-input v-model="form.platform" placeholder=""></el-input>
                </el-form-item>
            </div>
            <div class="line">
                <el-form-item label="" prop="score">
                    <el-input v-model="form.score" placeholder="+AAA 95"></el-input>
                </el-form-item>
                <el-form-item label="" prop="description">
                    <el-input v-model="form.description" placeholder="16"></el-input>
                </el-form-item>
            </div>
            <div class="line">
                <el-form-item label="" prop="online">
                    <el-date-picker type="date" placeholder="" v-model="form.online" style="width: 100%;"></el-date-picker>
                </el-form-item>
                <el-form-item label="" prop="finance">
                    <el-input v-model="form.finance" placeholder="xxxxWxxxx"></el-input>
                </el-form-item>
            </div>

            <el-form-item label="" prop="projectUrl">
                <el-input v-model="form.projectUrl" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="tags">
                <el-input v-model="form.tags.join(';')" placeholder="58AAA3"></el-input>
            </el-form-item>
            <el-form-item label="" prop="attentions">
                <el-input type="textarea" v-model="form.attentions" placeholder="50"></el-input>
            </el-form-item>
            <el-form-item label="" class="is-required">
                <div class="plan" v-for="(plan, index) in form.plan">
                    <div class="line">
                        <el-form-item label="">
                            <el-input v-model="plan.name" placeholder=""></el-input>
                        </el-form-item>
                        <el-form-item label="">
                            <div class="line-info">
                                <el-input v-model="plan.timeLimit" placeholder=""></el-input>
                                <div class="text"></div>
                            </div>
                        </el-form-item>
                        <el-form-item label="">
                            <div class="line-info">
                                <el-input v-model="plan.money" placeholder=""></el-input>
                                <div class="text"></div>
                            </div>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="plan.targetProject" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="line">
                        <el-form-item label="">
                            <div class="line-info">
                                <el-input v-model="plan.giveBack" placeholder=""></el-input>
                                <div class="text"></div>
                            </div>
                        </el-form-item>
                        <el-form-item label="">
                            <div class="line-info">
                                <el-input v-model="plan.sumProfit" placeholder=""></el-input>
                                <div class="text"></div>
                            </div>
                        </el-form-item>
                        <el-form-item label="">
                            <div class="line-info">
                                <el-input v-model="plan.yearlyProfit" placeholder=""></el-input>
                                <div class="text">%</div>
                            </div>
                        </el-form-item>
                        <el-form-item label="">
                            <el-input v-model="plan.serial" placeholder=""></el-input>
                        </el-form-item>
                    </div>
                    <div class="line">
                        <el-form-item label="">
                            <div class="line-list" v-for="(info, i) in plan.planInfo" :key="i">
                                <el-input v-model="info.text" placeholder=""></el-input>
                                <div class="btn">
                                    <el-button type="primary" size="small" plain @click="addPlanInfo(index, i+1)"></el-button><el-button type="danger" size="small" plain @click="deletePlanInfo(index, i)" v-if="i > 0"></el-button>
                                </div>
                            </div>
                        </el-form-item>
                    </div>
                    <div class="line">
                        <div class="addBtn">
                            <el-button type="success" plain @click="addPlan(index)"></el-button>
                            <el-button type="danger" plain @click="deletePlan(index)" v-if="index > 0"></el-button>
                        </div>
                    </div>
                </div>
            </el-form-item>
            <el-form-item align="center">
                <el-button type="primary" @click="onSubmit('form', '')"></el-button>
                <el-button type="success"  @click="$emit('open')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
  import {modifyMarks} from "../api/getdata"

  export default {
    name: "Entry",
    props: {
      marks: {
        default: {},
        type: Object
      },
      open: {
        default: false,
        type: Boolean
      }
    },
    data() {
      return {
        form: this.marks,
        rules: {
          type: [
            { required: true, message: '', trigger: 'blur' }
          ],
          logo: [
            { required: true, message: 'logo', trigger: 'blur' }
          ],
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          platform: [
            { required: true, message: '', trigger: 'blur' }
          ],
          score: [
            { required: true, message: '', trigger: 'blur' }
          ],
          description: [
            { required: true, message: '', trigger: 'blur' }
          ],
          online: [
            { required: true,  message: '', trigger: 'blur' }
          ],
          finance: [
            { required: true, message: '', trigger: 'blur' }
          ],
          projectUrl: [
            { required: true, message: '', trigger: 'blur' }
          ],
          tags: [
            { required: true, message: '', trigger: 'blur' }
          ],
          attentions: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        isShow: this.open,
      }
    },
    methods: {
      onSubmit(ruleForm, type) {
        this.form.status = 'draft'
        let data = this.form;
        let modifyMark = modifyMarks(this.form.id, data)
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            modifyMark.then(res => {
              this.$message({
                message: '',
                type: 'success'
              });
              this.$emit("open")
            })
          } else {
            return false;
          }
        });
      },
      addPlan(index) {
        let plan = {
          name: '', // 
          timeLimit: '', // ()
          targetProject: '', // 
          money: 0, // ()
          giveBack: '', // ()
          yearlyProfit: '', // (%)
          sumProfit: '', // ()
          serial: 0, // 
          planInfo: [{
            index: 0, // , 
            text: '', // 
          }]
        }
        console.log(this.form.plan[index])
        this.form.plan.push(plan)
      },
      deletePlan(index) {
        if (index > 0) {
          this.form.plan.splice(index, 1)
        } else {
          this.$message.error('');
        }
      },
      addPlanInfo(index, i) {
        let planInfo = {index: i, text: ''}
        this.form.plan[index].planInfo.push(planInfo)
        console.log(this.form.plan[index].planInfo)
      },
      deletePlanInfo(index, i) {
        if (i > 0) {
          this.form.plan[index].planInfo.splice(i, 1)
        } else {
          this.$message.error('');
        }
      },
      uploadImg(res, file) {
        if (res.output) {
          this.form.logo = res.output.path
        }else{
          this.$message.error('');
        }
      },
      beforeImgUpload(file) {
        const isRightType = (file.type === 'image/jpeg') || (file.type === 'image/png');
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isRightType) {
          this.$message.error(' JPG/PNG !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isRightType && isLt2M;
      },
    }
  }
</script>

