<template>
    <div class="giveback">
        <div class="header">
            <h3></h3>
            <div class="search">
                <el-input
                    v-model="search"
                    placeholder=""
                    suffix-icon="el-icon-search"
                    clearable
                ></el-input><el-button type="primary" :disabled="search.length == 0" @click="querySearch"></el-button>
            </div>
            <div class="header-select">
                <el-radio-group v-model="tab" size="small">
                    <el-radio-button label=""></el-radio-button>
                    <el-radio-button label=""></el-radio-button>
                    <el-radio-button label=""></el-radio-button>
                </el-radio-group>
            </div>
        </div>
        <el-table
                v-loading="loading"
                :data="list"
                style="width: 100%">
            <el-table-column
                    label=""
                    min-width="150">
                <template slot-scope="scope">
                    <span >{{ scope.row.project.name }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="80">
                <template slot-scope="scope">
                    <span >{{ scope.row.plan.id }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="100">
                <template slot-scope="scope">
                    <span >{{ scope.row.loanDate }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="80">
                <template slot-scope="scope">
                    <span >{{ scope.row.giveBack }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="120">
                <template slot-scope="scope">
                    <span >{{ scope.row.phone }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="100">
                <template slot-scope="scope">
                    <span >{{ scope.row.name }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="160">
                <template slot-scope="scope">
                    <span >{{ scope.row.account }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="120">
                <template slot-scope="scope">
                    <span >{{ scope.row.realName }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" min-width="210">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            type="info"
                            v-if="scope.row.status == ''"
                            disabled
                            plain
                            @click="handleGiveBack(scope.$index, scope.row)"></el-button>
                    <el-button
                            v-if="scope.row.status == ''"
                            size="mini"
                            type="primary"
                            @click="handleGiveBack(scope.$index, scope.row)"></el-button>
                    <el-button
                            v-if="scope.row.status == ''"
                            size="mini"
                            type="danger"
                            @click="handleRefuse(scope.$index, scope.row)"></el-button>
                    <el-button
                            v-if="scope.row.status == ''"
                            size="mini"
                            type="warning"
                            plain
                            disabled
                            @click="handleRefuse(scope.$index, scope.row)"></el-button>
                    <el-button
                            v-if="scope.row.status == ''"
                            size="mini"
                            disabled
                            @click="handleRefuse(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="page" v-show="count >= 10">
            <div class="page-init"></div>
            <el-pagination
                    background
                    layout="prev, pager, next"
                    @current-change="handleCurrentChange"
                    :page-size=10
                    :total="count">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import {getGiveBack, setGiveBack} from "../api/getdata"

    export default {
        name: "Back",
        data() {
          return {
            loading: true,
            list: [],
            count: 0,
            search: '',
            tab: ''
          }
        },
        created() {
          this.getGiveBackData()
        },
        methods: {
          getGiveBackData(data) {
            let backData = getGiveBack(data)
            backData.then(res => {
              console.log(res)
              this.list = res.output.rows
              this.count = res.output.count
              this.loading = false
            })
          },
          handleRefuse(index, val) {
            console.log(index, val)
            this.$prompt('', '', {
              confirmButtonText: '',
              cancelButtonText: '',
            }).then(({ value }) => {
              let setBack = setGiveBack(val.id, {status: '', rejectReason: value})
              setBack.then(res => {
                console.log(res)
                this.list[index].status = ''
                this.$message({
                  type: 'success',
                  message: ': ' + value
                });
              })

            })
          },
          handleGiveBack(index, val) {
            console.log(index, val)
            this.$confirm(', ?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
              let setBack = setGiveBack(val.id, {status: ''})
              setBack.then(res => {
                console.log(res)
                this.list[index].status = ''
                this.$message({
                  type: 'success',
                  message: '!'
                })
              })
            }).catch(() => {

            });


          },
          handleCurrentChange(val) {
            this.page = val
            this.loading = true
            this.getGiveBackData({index: this.page})
          },
          querySearch() {
              this.getGiveBackData({name: this.search})
          }
        },
        watch: {
          tab() {
            if (this.tab == '') {
                this.getGiveBackData()
            } else if (this.tab == '') {
                this.getGiveBackData({status: ''})
            }else if (this.tab == '') {
                this.getGiveBackData({status: ''})
            }
          }
        }
    }
</script>

