    .page
        display: flex
        padding: 20px
        &-init
            flex: auto
    .header
        padding-bottom: 20px
        border-bottom: 1px solid #f2f2f2
        margin-bottom: 10px
        margin-right: -30px
        display: flex
        align-items: center
        justify-content: space-between
        .header-select
            margin-right: 30px
        .search
            display: flex
            width: 450px
            .el-input
                margin-right: 8px
        h3
            font-size: 16px
</style>