<template>
    <div class="staff">
        <div class="header">
            <h3></h3>
        </div>
        <el-table
                v-loading="loading"
                :data="list"
                style="width: 100%">
            <el-table-column
                    label=""
                    min-width="80">
                <template slot-scope="scope">
                    <span >{{ scope.row.account }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="80">
                <template slot-scope="scope">
                    <span >{{ scope.row.level }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="150">
                <template slot-scope="scope">
                    <span >{{ scope.row.title }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="100">
                <template slot-scope="scope">
                    <span >{{ scope.row.password }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="80">
                <template slot-scope="scope">
                    <span >{{ scope.row.realName }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="180">
                <template slot-scope="scope">
                    <span >{{ scope.row.idCard }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label=""
                    min-width="120">
                <template slot-scope="scope">
                    <span >{{ scope.row.phone }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" min-width="210">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)"></el-button>
                    <el-button
                            size="mini"
                            type="primary"
                            @click="handleAdd(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="page" v-show="count > 8">
            <div class="page-init"></div>
            <el-pagination
                    background
                    layout="prev, pager, next"
                    @current-change="handleCurrentChange"
                    :page-size=8
                    :total="count">
            </el-pagination>
        </div>
        <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form :model="form" label-position="left">
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input type="number" v-model="form.account" placeholder="0002"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input type="number" v-model="form.level" placeholder="0"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input type="text" v-model="form.title" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input type="text" v-model="form.password" placeholder="8"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input type="text" v-model="form.realName" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input type="number" v-model="form.idCard" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input type="number" v-model="form.phone" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false"> </el-button>
                <el-button type="primary" @click="submitForm"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import {getStaffList, addStaff, deleteStaff, putStaff} from "../api/getdata";

    export default {
        name: "Staff",
        data() {
            return {
                list: [],
                loading: true,
                page: 1,
                count: 0,
                type: '',
                editId: null,
                dialogFormVisible: false,
                formLabelWidth: '120px',
                form: {
                    account: '', // 
                    password: '', // 
                    level: 0, // 
                    title: '', // 
                    realName: '', // 
                    idCard: '', // 
                    phone: '', // 
                },
            }
        },
        created() {
          this.userList()
        },
        methods: {
            userList() {
                let user = getStaffList({page: this.page})
                user.then(res => {
                    this.list = res.output.rows
                    this.count = res.output.count
                    this.loading = false
                })
            },
            handleCurrentChange(val) {
                this.page = val
                this.userList()
            },
            handleEdit(index, val) {
                console.log(val)
                this.dialogFormVisible = true
                this.form = val
                this.type = 'edit'
                this.editId = val.id
            },
            handleDelete(index, val) {
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    let delUser = deleteStaff(val.id)
                    delUser.then(res => {
                        this.list.splice(index, 1)
                        console.log(res)
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                    })
                }).catch(() => {

                })

            },
            handleAdd() {
                this.dialogFormVisible = true
                this.type = 'add'
            },
            submitForm() {
                if (this.type == 'add') {
                    let addUserData = addStaff(this.form)
                    addUserData.then(res => {
                        this.dialogFormVisible = false
                        console.log(res)
                        this.$message({
                            message: '!',
                            type: 'success'
                        });
                        this.form = {}
                    })
                } else if (this.type == 'edit') {
                    let data = putStaff(this.editId, this.form)
                    data.then(res => {
                        console.log(res)
                        this.dialogFormVisible = false
                        this.$message({
                            message: '!',
                            type: 'success'
                        });
                    })
                }

            },
        }
    }
</script>

<style scoped lang="sass">
    .el-form-item
        margin-right: 20px
    .page
        display: flex
        padding: 20px
        &-init
            flex: auto
    .header
        padding-bottom: 10px
        border-bottom: 1px solid #f2f2f2
        margin-bottom: 10px
        margin-right: -30px
        h3
            font-size: 16px
    .el-form
        display: flex
        flex-wrap: wrap
    .staff
        .el-table td
            padding: 8px 0
</style>